
*{margin:0; padding: 0;
}
html {
  height: 100%;
}
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /*align-items: flex-start;*/
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  /*height: 100%;*/
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /*color:  #2E8B57; */ /*#2c3e50;*/
  margin-top: 10%;
  width: 80%;
}
h1 {
  font-size: 3rem;
  color:  #2E8B57;
}
#write {
  width: 72%;
  height: 2rem;
  margin: 20px 0;
  border: 1px solid #ccc;
  border-radius: 0.2rem;
}
#write:focus {
  outline: 0;
  border: 1px solid #39B662;
  box-shadow: 0 0 4px #39B662;
}
button {
  margin-left: 2%;
  vertical-align: middle;
  width: 23%;
  height: 2rem;
  background-color: #3CB371;
  border: 0;
  color: #FFF;
  border-radius: 0.3rem;
}
form {
  margin-bottom: 2rem;
}
form div {
  color:  #000;
  text-align: left;
  font-size: 1.1rem;
  margin: 0.6rem 0;
}
label {
  display: inline-block;
  width: 93%;
  word-wrap: break-word;
}
label input {
  width: 1.2rem;
  height: 1.2rem;
  vertical-align: middle;
  margin-bottom: 2px;
}
form span {
  color: #F00;
}
.finished {
  color: #39B662;
}
#clc {
  width: 100%;
  position: fixed;
  bottom: 0;
  /*left: 50%;*/
  margin-left: -10%;
  cursor: pointer;
  background-color: #39B662;
  color: #FFF;
  height: 2rem;
}
#clc span:first-child {
  border-right: 1px solid #FFF;
  padding-right: 3%;
}
#clc span {
  display: inline-block;
  width: 40%;
  height: 2rem;
  line-height: 2rem;
}



